#navBar {
  height: 50px;
  font-size: 26px;
  font-weight: bold;
}

#navBar > div {
  height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

#navBar > div > span {
  cursor: pointer;
}

#navBar > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

#navBar > ul > li {
  display: inline-block;
  border-right: 2px solid white;
}